:root {
  --light-blue: #00acdc;
}

body {
  margin: 0;
  font-family: Roboto, Arial, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

.header {
  box-sizing: border-box;
  text-align: center;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 60px;
  padding-left: 50px;
  padding-right: 50px;
  display: flex;
}

.header-logo {
  display: block;
}

.nav__list {
  padding-inline-start: 0;
  display: inline;
}

.nav__item {
  display: inline-block;
}

.links {
  text-align: right;
  width: 636px;
  height: 60px;
  line-height: 60px;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  height: 60px;
  margin-left: 17px;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.is-active:after {
  content: "";
  background-color: var(--light-blue);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: inline-block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.is-active {
  color: var(--light-blue);
  height: 60px;
  margin-left: 0;
  position: relative;
}

.nav__link:hover, .nav__link:focus {
  color: var(--light-blue);
}
